


#contact {
    background-color:black;
    /* min-height: ; */
    width: 100%;
    margin:0%;
    overflow: hidden;
}
.Contact-head {
    margin: 0%;
    padding: 0%;
    padding-left: 0.2em;
    padding-top: 1em;
    height: 3em;
    min-height: max-content;
    width: 100%;
    text-align: center;
    text-decoration:2px solid;
    text-decoration-line: underline;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    letter-spacing: 0.3em;
    font-weight:100;
    color:aliceblue;
}
#contact-msg {
    /* position:relative ; */
   /* bottom: 5vh; */
    height:fit-content;
    width: 100%;
    min-height: fit-content;
    margin: 0%;
    margin-top: 15%;
    padding: 0;
    display: grid ;
    justify-content: center;
    align-items: center;
}
#contact-msg-field {
    width: 100%;
    min-width:30em;
    height: inherit;
    margin: 0%;
    /* position: relative; */
    padding: auto;
    background-color: rgba(222, 224, 219, 0.877);
}
.contact-msg-button-list {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    
}
.contact-msg-button {
    width: 120px;
    /* min-width: 120px; */
}
#contact-container {
    /* display:block; */
    width: 70%;
    justify-content: space-around;
    align-items: center;
    height:max-content;
    column-gap: 0.3em;
    row-gap: 2em;
    margin: 15%;
    margin-top: 15%;
    
    padding: auto;
}
.contact-element {
    padding: 2px;
    margin: auto;
}
.contact-button {
    width: 10rem;
    height: max-content;
    padding: 1px;
    margin: 0%;
}
