
#main {
    height: 100vh;
    background-color: rgb(37, 209, 172);
    margin: 0%;
    overflow: hidden;
}
.head {
    margin: 0%;
    padding: 0%;
    padding-left: 0.2em;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    letter-spacing: 0.3em;
    font-size:8em;
    font-weight:100;
    color:aliceblue;
}
#descrip {
    width: 100%;
    background-color: black;
    color: aliceblue;
    text-align: center;
    height: 0%;
    transition: all 2s;
    font-family: Arial, Helvetica, sans-serif;
    letter-spacing: 0.3rem;
    padding-top:0em ;
    padding-bottom: auto;
    font-size: 1em;
    margin-top:1.2em;
    margin-bottom: 1.2em;
    overflow: hidden;

}
#list {
    padding-top: 2em;
    width: 10em;
    margin: auto;
    font-size: xx-large;
    justify-content:space-evenly;
}
 
.list-item {
    text-align: center;
    padding: 1em;
    margin: 0%;
    font-family:cursive;


}
.list-buttons {
    background-color: aqua;

}

