

#intro {
    width: 100%;
    height:100vh;
    background-image: linear-gradient(rgba(60, 15, 184, 0.692), rgba(0, 128, 96, 0.664));
   overflow: hidden;
   margin: 0%;
   }

#profile-pic{
    border:4px double rgb(15, 218, 174);
    border-radius: 40%;
    box-shadow: 5px 5px 10px #25cabc67 ;
    margin:5em;
    rotate: 15deg;
    width:13em;

    
}

#head { 
    font-size: x-large;
    padding: auto;
    margin-top: 10%;
    font-weight: bolder;
}
#introCard {
    min-width: fit-content;
    width: 60%;
    font-size: large;
    /* height: auto; */
    min-height: 30vh;
    margin: auto;
    padding:auto;
    padding-left: 3em;
    padding-right: 3em;
    background-color: rgba(235, 215, 215, 0.226);
    color:black;
    font-family:Arial, Helvetica, sans-serif ;

}
.parts {
    width:100%;
    text-align:left;

}


/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
    #profile-pic {
        width:14em;
     }
     #introCard {
        width: 60%;
        font-size: large;
     }
     #head {
        font-size: x-large;
     }
     .parts {
        
        font-weight: normal;
     }
}
/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
    #profile-pic {
        width:15em;
     }
     #introCard {
        width: 15em;
        font-size: larger;
     }
     #head {
        font-size: xx-large;
     }
     .parts {
        
    font-weight: normal;
     }

}
/* Large devices (laptops/desktops, 892px and up) */
@media only screen and (min-width: 892px) {
    #profile-pic {
        width:17em;
     }
     #introCard {
        width: 16em;
        font-size: larger;
     }
     #head {
        font-size: xx-large;
     }
     .parts {
        
        font-weight: normal;
         }
}
/* Large devices (laptops/desktops, 1100px and up) */
@media only screen and (min-width: 1100px) {
    #profile-pic {
        width:20em;
     }
     #introCard {
        width: 17em;
        font-size: x-large;
     }
     #head {
        font-size: xx-large;
     }
     .parts {
        
        font-weight: normal;
         }
}
/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
    #profile-pic {
        width:22em;
     }
     #introCard {
        width: 20em;
        font-size: x-large;
     }
     #head {
        font-size: xx-large;
     }
     .parts {
        
        font-weight: normal;
         }
}
@media only screen and (min-width: 1400px) {
 #profile-pic {
    width:25em;
 }
 #introCard {
    width: 25em;
    font-size: x-large;
 }
 #head {
    font-size: xx-large;
 }
 .parts {
        
    font-weight: normal;
     }
}